.header {
  grid-area: header;
  background-color: #e2dccd;
  display: grid;
  grid-template-columns: 150px 950px 100px;
}

.header img {
  width: 80px;
  margin: 10px 35px;
}

.button {
  width: 70px;
  height: 30px;
  background-color: #83bb75;
  border-radius: 15px;
}

.login {
  display: grid;
  grid-template-rows: 50px 1fr;
  grid-column: 3/4;
}

.login_block {
  padding-top: 15px;
}

.auth {
  grid-column: 3/4;
}
